// function that returns all parameters of canvas
export function canvasCreate(context) {
	const selection = context.selection;
	const doc = context.document;
	const page = "";
	const canvasCount = selection.length;

	let canvas = {
		layer : selection[0],
		height : selection[0].frame().height(),
		width : selection[0].frame().width(),
		x : selection[0].frame().x(),
		y : selection[0].frame().y(),
		doc : doc
	}

	return canvas;
}

export function canvasCreateMulti(context) {
	const selection = context.selection;
	const doc = context.document;
	const page = "";
	const canvasCount = selection.length;
	let heights = [],
		widths = [],
		xs = [],
		ys = [];

	for (let i = 0; i < selection.length; i++) {
		heights[i] = selection[i].frame().height();
		widths[i] = selection[i].frame().width();
		xs[i] = selection[i].frame().x();
		ys[i] = selection[i].frame().y();
	}

	let canvas = {
		layer : selection,
		height : heights,
		width : widths,
		x : xs,
		y : ys,
		doc : doc
	}

	return canvas;
}

export function fetchData(popupDataObj){
	let url = popupDataObj.url,
		keys = popupDataObj.key,
		dataTable = new Array(),
		dataMax = new Array(),
		dataMin = new Array();
	
	return fetch(url)
		.then(res => res.json())
		.then(dataJSON => {

			//return an array of values that match on a certain key
			function getValues(obj, key) {
				var objects = [];
				for (var i in obj) {
				    if (!obj.hasOwnProperty(i)) continue;
				    if (typeof obj[i] == 'object') {
				        objects = objects.concat(getValues(obj[i], key));
				    } else if (i == key) {
				        objects.push(obj[i]);
				    }
				}
				return objects;
			}

			for(let i in keys){
				let row = getValues(dataJSON, keys[i]);
				for (let j in row) {
					row[j] = parseFloat(row[j]).toFixed(2);
				}
				dataTable[i] = row;
				dataMax[i] = Math.max( ...dataTable[i] );
				dataMin[i] = Math.min( ...dataTable[i] );
			}

			let data = {
				table : dataTable,
				max : Math.max( ...dataMax ),
				min : Math.min( ...dataMin ),
				rows : dataTable.length,
				columns : dataTable[0].length
			}

			return data;
		}); 
}

export function processData(type) {
	// function that search non numeric data in pasteBoard
	let pasteBoard = NSPasteboard.generalPasteboard();
	const stringPasteboard = String(pasteBoard.stringForType(NSPasteboardTypeString));

	function searchInPasteBoard(pasteBoard){
		let onlyNumbers = pasteBoard.replace(/[^\w]/g, "");
		let letterReg = /[a-zA-z]/;
		return letterReg.test(onlyNumbers);
	}

	let letters = searchInPasteBoard(stringPasteboard);
	// if 0  only numbers

	let dataObj,
		popupDataObj = {
			key : ""
		},
		dataTable = new Array(),
		dataMax = new Array(),
		dataMin = new Array();

	switch(letters){
		case false: {
			function dataCreate(pasteBoard){
				let dataRows = pasteBoard.replace(/[\t]/g, ", ").split("\n");
				for (let i in dataRows){
					dataTable[i] = dataRows[i].replace(/[\s]/g, "").split(",");
					for (let j in dataTable[i]) {
					    dataTable[i][j] = parseFloat(dataTable[i][j]);
					}
					dataMax[i] = Math.max( ...dataTable[i] );
					dataMin[i] = Math.min( ...dataTable[i] );

				}

				// For MAX and MIN add support of stacked charts
				let data = {
					table : dataTable,
					max : Math.max( ...dataMax ),
					min : Math.min( ...dataMin ),
					rows : dataTable.length,
					columns : dataTable[0].length
				}

				return data;
			};

			dataObj = dataCreate(stringPasteboard);
			return [dataObj, false];
			break;
		}
		case true: {
			// Make sure that Sparklines, Progress Bar and Gauge Chart is OK

			function dataRequestPopup(nameOne, nameTwo){
				let randomView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 40.0))

			    let rowsInput = NSTextField.alloc().initWithFrame(NSMakeRect(0.0, 15.0, 120.0, 25.0))
				    rowsInput.cell().setPlaceholderString(nameOne)
				    randomView.addSubview(rowsInput)

				let columnsInput = "";
				if ( nameTwo != ""){
				    columnsInput = NSTextField.alloc().initWithFrame(NSMakeRect(140.0, 15.0, 120.0, 25.0))
				    columnsInput.cell().setPlaceholderString(nameTwo)
				    randomView.addSubview(columnsInput)
				}

				let apiView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 75.0))

				let keyJSON = NSTextField.alloc().initWithFrame(NSMakeRect(0.0, 15.0, 260.0, 25.0))
				    keyJSON.cell().setPlaceholderString("Type keys in JSON to visualize it")
				    apiView.addSubview(keyJSON)

				let urlJSON = NSTextField.alloc().initWithFrame(NSMakeRect(0.0, 50.0, 260.0, 25.0))
				    urlJSON.cell().setPlaceholderString("Type url to JSON")
				    apiView.addSubview(urlJSON)

			    let alert = COSAlertWindow.new();
				    alert.setMessageText("Data for Chart")
				    alert.addTextLabelWithValue("Random data")
				    alert.addAccessoryView(randomView)
				    alert.addTextLabelWithValue("or data from JSON")
				    alert.addAccessoryView(apiView)
				    alert.addButtonWithTitle("OK")
				    alert.addButtonWithTitle("Cancel")

				alert.alert().window().setInitialFirstResponder(rowsInput);

				if (nameTwo != ""){
					rowsInput.setNextKeyView(columnsInput);
				}
				urlJSON.setNextKeyView(keyJSON);

				// Run popup
				let responseCode = alert.runModal();

				let rowsCount = rowsInput.stringValue(),
					columnsCount = columnsInput.stringValue(),
					urlJSONString = urlJSON.stringValue(),
					keyJSONString = "";

					let test = keyJSON.stringValue();

					if (test.length() > 1) {
						keyJSONString = keyJSON.stringValue().replace(/[\s]/g, "").split(",");
					}
				let popupData = {};

				return popupData = {
					rows : rowsCount,
					columns : columnsCount,
					url : urlJSONString,
					key : keyJSONString
				}
			}

			popupDataObj = dataRequestPopup("Categories","Items")

			// Random data generaton
			function createRandomData(rows, columns, type){
				let dataMaxNumber = 100,
					dataRow = new Array();

				for (let i = 0; i < rows; i++) {
					if (type == "circle") {

						let startRandNumber = 60,
							counterNumber = 0;

						for (let j = 0; j < columns - 1; j++){
							dataRow[j] = Math.round(Math.random() * (dataMaxNumber - startRandNumber));
							counterNumber = counterNumber + dataRow[j];
							startRandNumber = counterNumber;
						}
						dataRow[columns - 1] = dataMaxNumber - counterNumber;

					} else {
						for (let j = 0; j < columns; j++) {
							// Improve random data generation algorithm !!!
							dataRow[j] = (Math.random() * dataMaxNumber).toFixed(0);
						}
					}

					dataTable[i] = dataRow;
					dataRow = [];
					dataMax[i] = Math.max( ...dataTable[i] );
					dataMin[i] = Math.min( ...dataTable[i] );
				}

				let data = {
					table : dataTable,
					max : Math.max( ...dataMax ),
					min : Math.min( ...dataMin ),
					rows : dataTable.length,
					columns : dataTable[0].length
				}

				return data;
			};

			if (popupDataObj.url != "") {

				return [popupDataObj, true];

			} else {
				dataObj = createRandomData(popupDataObj.rows, popupDataObj.columns, type);
				return [dataObj, false];
			}

			break;
		}
	}	

}